<template>
    <div>
        <div class="mb-3">
            <p class="statistic__title">
                Всего собранно - {{ wavesBalance }} <WavesIcon class="statistic__title-icon" />
                <button
                    v-on:click="refreshStatistic()"
                    class="statistic__refresh"
                >
                    Обновить <RefreshIcon class="statistic__refresh-icon" />
                </button>
            </p>
        </div>
        <div class="mb-3" v-if="goal !== 0">
            <div class="goal__block">
                <div class="goal__percentage" :style="`width: ${goalPercentage}%`"></div>
                <span class="goal__num">
                    {{ goalPercentage.toFixed(2) }}%
                </span>
            </div>
        </div>
        <div v-if="chainLoaded">
            <div
                class="table__block"
                v-if="chainData.length > 0"
            >
                <div class="table__row table__row--header">
                    <div class="table__item table__item--address">
                        От кого
                    </div>
                    <div class="table__item table__item--comment">
                        Комментарий
                    </div>
                    <div class="table__item table__item--amount">
                        Сколько
                    </div>
                </div>
                <div class="table__body">
                    <div
                        class="table__row"
                        v-for="(item, key) in chainData"
                        v-bind:key="key"
                        v-on:click="openData(item)"
                    >
                        <div class="table__item table__item--address">
                            {{ item.sender }}
                        </div>
                        <div class="table__item table__item--comment">
                            <span v-if="item.attachment !== undefined">
                                {{ item.attachment | decodeAttachment }}
                            </span>
                        </div>
                        <div class="table__item table__item--amount">
                            {{ item.amount / 10**8 }} <WavesIcon class="icon" />
                        </div>
                        <div v-if="item.active && item.active !== undefined" class="table__content">
                            <div class="table__content-item">
                                <span class="table__content-label">
                                    ID транзакции
                                </span>
                                <p class="table__content-description">
                                    {{ item.id }}
                                </p>
                            </div>
                            <div class="table__content-item">
                                
                                <span class="table__content-label">
                                    От кого
                                </span>
                                <p class="table__content-description">
                                    {{ item.sender }}
                                </p>
                            </div>
                            <div class="table__content-item" v-if="item.attachment !== undefined || item.attachment.length > 0">
                                <span class="table__content-label">
                                    Комментарий
                                </span>
                                <p class="table__content-description">
                                    {{ item.attachment | decodeAttachment }}
                                </p>
                            </div>
                            <div class="table__content-item">
                                <span class="table__content-label">
                                    Сколько
                                </span>
                                <p class="table__content-description">
                                    {{ item.amount / 10**8 }} <WavesIcon class="icon" />
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-else>
            <p>
                Загрузка
            </p>
        </div>
    </div>
</template>

<script>

import WavesIcon from '@/assets/waves-icon.svg';
import RefreshIcon from '@/assets/refresh-icon.svg';

import { nodeInteraction } from "@waves/waves-transactions";
import { base58Decode, bytesToString } from '@waves/ts-lib-crypto';

export default {
    name: 'Statistics',
    data() {
        return {
            address: null,
            chainData: [],
            wavesBalance: 0,
            chainLoaded: false,
        }
    },
    components: {
        WavesIcon,
        RefreshIcon
    },
    mounted() {
        this.refreshStatistic();
        this.$root.$on('refresh_statistic', this.refreshStatistic);
        setInterval(() => {
            this.refreshStatistic();
        }, 30000)
    },
    filters: {
        decodeAttachment(value) {
            return bytesToString(base58Decode(value));
        }
    },
    props: {
        goal: {
            type: Number,
            default: 0
        },
        recipient: {
            type: String,
            default: '',
        }
    },
    computed: {
        goalPercentage() {
            let percent = (100 / this.goal) * this.wavesBalance;
            return percent;
        }
    },
    methods: {
        openData(item) {
            if (item.active === undefined) {
                this.$set(item, 'active', true);
                return false;
            }
            item.active = !item.active;
        },
        changeAddress(address) {
            this.address = address
        },
        refreshStatistic() {
            this.axios
                .get(`${this.$config.statisticUrl}/transactions/address/${this.recipient}/limit/10`)
                .then((response) => {
                    this.chainData = response.data[0];
                    this.getBalance();
                    if (!this.chainLoaded) {
                        this.chainLoaded = true;
                    }
                },
                (error) => {
                    console.error(error);
                })
        },
        async getBalance() {
            let wavesBalance = await nodeInteraction.balanceDetails(this.recipient, this.$config.nodeUrl);
            this.wavesBalance = wavesBalance.available / 10**8;
        }
    },
}
</script>

<style src="./scss/_index.scss" lang="scss" scoped></style>
