<template>
    <div class="donate__block">
        <div class="donate__user-block" v-if="user.address !== null">
            <span class="donate__user-title">
                Текущий адрес
            </span>
            <span class="donate__user-address">
                {{ user.address }}
            </span>
        </div>
        <div class="donate__user-block" v-else>
            <button
                v-on:click="wavesLogin()"
                class="button button--active"
            >
                Войти в аккаунт
            </button>
        </div>
        <div class="mb-3">
            <label class="form-label">Введите сумму</label>
            <div class="donate__input-block">
                <input type="number" min="1" class="form-control donate__input" v-model="amount">
                <WavesIcon class="input-icon" />
            </div>
        </div>
        <div
            class="mb-3"
            :class="{'error' : comment.length > 140}"
        >
            <label class="form-label">Введите комментарий {{ comment.length }}/140</label>
            <textarea class="form-control donate__input" rows="5" v-model="comment"></textarea>
        </div>
        <button
            v-on:click="donate()"
            class="button"
            :class="[amount > 0 ? 'button--active' : 'button--default']"
        >
            Задонатить
        </button>
    </div>
</template>

<script>

import WavesIcon from '@/assets/waves-icon.svg';
import { base58Encode, stringToBytes } from '@waves/ts-lib-crypto';

export default {
    name: 'DonateForm',
    components: {
        WavesIcon,
    },
    data() {
        return {
            asset: 'WAVES',
            amount: 1,
            comment: '',
            currencies: this.$config.currencies,
            user: {
                address: null,
                balance: 0,
            }
        }
    },
    props: {
        recipient: {
            type: String,
            default: '',
        }
    },
    computed: {
        transferData() {
            let data = {
                recipient: this.recipient,
                amount: this.amount * 10**8,
                assetId: this.asset.assetId,
                attachment: base58Encode(stringToBytes(this.comment)),
            };
            return data;
        }
    },
    methods: {
        async donate() {
            if (this.amount === "0") {
                return false;
            }

            if (this.comment.length > 140) {
                this.$swal
                    .fire({
                        icon: 'error',
                        title: 'Ошибка',
                        text: 'Комментарий не может быть больше 140 символов',
                    });
                return false;
            }

            await this.$signer
                    .transfer({
                        ...this.transferData
                    })
                    .broadcast()
                    .then((response) => {
                        this.$swal
                            .fire({
                                icon: 'success',
                                title: 'Успех!',
                                html: `Ваш донат принят!<br>ID транзакции - ${response.id}`,
                            });
                        this.user.address = response.sender;
                        this.$root.$emit('refresh_statistic')
                    },
                    (error) => {
                        console.error(error)
                    })
        },
        wavesLogin() {
            this.$signer
                .login()
                    .then((response) => {
                        this.user.address = response.address;
                        this.getUserBalance()
                    },
                    (error) => {
                        console.error(error)
                    })
        },
        getUserBalance() {
            this.$signer
                .getBalance()
                    .then((response) => {
                        for (let key in response) {
                            if (response[key].assetId === 'WAVES') {
                                this.user.balance = response[key].amount / 10**8;
                                return false;
                            }
                        }
                    },
                    (error) => {
                        console.error(error)
                    })
        }
    },
}
</script>

<style src="./scss/_index.scss" lang="scss" scoped></style>
